<template lang="">
    <div>
        <el-card class="box-card">
    <template #header>
      <div class="card-header">
        <h1 class="blog-title">{{x.title}}</h1>
        <div class="blog-time">{{x.time}}</div>
      </div>
    </template>
    <div class="text item" v-html="x.contant"></div>
    <el-icon @click="comment()"><ChatDotRound /></el-icon>
    <span class="comment">评论区</span>
    <el-divider>
      <el-icon><star-filled /></el-icon>
    </el-divider>
    <ul>
      <li class="comment-item" v-for="(item,index) in getList1" :key="index">
        {{item.contant}}
      </li>
    </ul>
  </el-card>
  <el-dialog v-model="dialogVisible" title="评论文章" width="50%">
        <el-input v-model="input2" class="w-50 m-2" placeholder="评论" @change="handleInput" />
      </el-dialog>
    </div>
</template>
<script setup>
import { ChatDotRound, StarFilled } from '@element-plus/icons-vue'
import { ref, reactive, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import { useRoute } from 'vue-router'
const commitDataList = ref([])
const getList = ref([])
const getList1 = ref([])
const route = useRoute()
let dataList = ref([])
const input2 = ref('')
let x = ref([])
let dialogVisible = ref(false)



onMounted(() => {
  dataList.value = JSON.parse(localStorage.getItem('blogList'))

  console.log(route.params.id)
  x.value = dataList.value[route.params.id]
  console.log(x.value)
  getCommitList()
// 获取评论列表

})

const getCommitList = ()=>{
  getList.value =  JSON.parse(localStorage.getItem("commitList"))
  for(let item of getList.value){
    if(item.id === route.params.id){
      getList1.value.push(item)
    }
  }
  console.log(getList1.value);
}

// 添加评论
const handleInput = ()=>{
  const commitList = reactive({
        id: route.params.id,
        contant: input2.value
      })
      commitDataList.value.unshift(commitList)
      localStorage.setItem('commitList', JSON.stringify(commitDataList.value))
      window.location.reload()
      ElMessage.success('添加成功')
}

const comment = () => {
  dialogVisible.value = !dialogVisible.value
}
</script>
<style lang="scss">
.el-dialog{
z-index: 999 !important;
}
.el-icon {
  cursor: pointer;
}

.card-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.box-card {
  width: 100%;
  position: relative;
  .comment {
    margin-left: 45%;
    // transform: translateX(-50%);
  }
  .comment-item {
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 3px;
  }
}
</style>